@import url(https://fonts.googleapis.com/css?family=Permanent+Marker&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root{
  height: 100%;
}
.App {
  height: 100%;
  display : flex;
  justify-content: center;
  align-items: center;
  background-color: #95afc0;

  font-family: 'Lucida Console', monospace;
}

.board-wrapper {
  position: relative;
}

.game-info-wrapper {
  position: absolute;
  left: -20px;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  padding : 15px;
  border: 2px solid #535c68;
  border-radius: 5px;
  font-size: 20px;
  min-width: 170px;
}

.game-info-wrapper .title{
  margin: 0 0 10px;
  font-size: 25px;
}

.game-info-wrapper .player{
  font-size: 25px;
  font-family: 'Permanent Marker', cursive;
}

.game-info-wrapper .game-info > p {
  margin: 0 0 7px;
}

.board-row{
  display: flex;
}

.board-cell {
  width: 30px;
  height: 30px;
  text-align: center;
  line-height: 30px;
  font-size: 30px;
  font-weight: 900;
  font-family: 'Permanent Marker', cursive;

  margin: 1px;
  padding: 0;
  background-color: #fff;
}

button.board-cell{
  outline: 0;
  border : 0;
  box-sizing: border-box;
  cursor: pointer;
}

.player.X{
  color: #eb4d4b;
}
.player.O{
  color: #4834d4;
}

.player.draw{
  color: #27ae60;
}

.board-cell.X, .board-cell.O{
  cursor: not-allowed;
}

.result-wrapper {
  font-size: 30px;
}

.result.player{
  font-size: 90px;
  font-weight: 900;
  font-family: 'Permanent Marker', cursive;
}

.text-center {
  text-align: center !important;
}
